@import '../../scss/index';
.thumbnail {
  p {
    text-align: center;
  }
  img {
    width: 100%;
    border-radius: $border-radius;
    border: 4px solid transparent;

    transition: border-color 0.2s ease-in;
  }
  &:hover {
    img {
      border: 4px solid $main-color;
    }
  }
}
